import React, { useState } from "react";

const Modal = ({ closeModal }) => {
  return (
    <div className="modal-dark-background ">
      <div className="file-downloading-modal">
        <h2>Successfully Submitted</h2>
        <p>We will get back to you soon.</p>
        <button className="form-button" onClick={closeModal}>
          Close
        </button>
      </div>
    </div>
  );
};

const FormInput = () => {
  const [fName, setFname] = useState("");
  const [lName, setlName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [showModal, setShowModal] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();

    if (fName.length === 0) {
      alert("Fill up the First Name");
    } else if (lName.length === 0) {
      alert("Fill up the Last Name");
    } else if (email.length === 0) {
      alert("Fill up the email");
    } else if (phone.length === 0) {
      alert("Fill up the phoneNumer");
    } else {
      setShowModal(true);
      setFname("");
      setlName("");
      setEmail("");
      setPhone("");
    }
  };

  const closeModal = () => {
    setShowModal(false);
  };

  return (
    <div className="parent-form">
      <form onSubmit={handleSubmit} className="parent-form">
        <input
          className="input-fields"
          type="text"
          value={fName}
          onChange={(e) => setFname(e.target.value)}
          placeholder="Enter First Name"
        ></input>
        <input
          className="input-fields"
          type="text"
          value={lName}
          onChange={(e) => setlName(e.target.value)}
          placeholder="Enter Last Name"
        ></input>
        <input
          className="input-fields"
          type="email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          placeholder="Enter Email"
        ></input>
        <input
          className="input-fields"
          type="tel"
          value={phone}
          onChange={(e) => setPhone(e.target.value)}
          pattern="[0-9]{10}"
          placeholder="Enter PhoneNumber"
        ></input>
        <select className="input-fields">
          <option>Type of option</option>
          <option>Type1</option>
          <option>Type2</option>
          <option>Type3</option>
        </select>
        <button type="submit" className="input-fields submit-button">
          SCHEDULE NOW
        </button>
      </form>

      {showModal && <Modal closeModal={closeModal} />}
    </div>
  );
};

export default FormInput;
